@import "../../global.scss";


.portfolio {

.card-display {
    display:flex;
    flex-flow:row wrap;
    align-items: baseline;


.card {
    height: 60vh;
    width: 30%;
    margin: 1.5%;
    
    .card-header {
        overflow: hidden;
        background-color: #06f;
        text-align: center;
        height: 30%;
        
        .repo-title {
            height: 35%;
            font-size: 27px;
            overflow: hidden;
            color: white;
            margin-top: 20px;
            
        }

        .language-list {
            display: flex;
            flex-wrap: wrap; 
            overflow-y: scroll;
            justify-content: center;
            gap: 10px; 
            max-height: 50%; 

            .language {
                background-color: lightsalmon;
                margin: 5px;
                font-size: 15px;
                padding: 5px 8px;
                border-radius: 20px;
                font-weight: bold;
                white-space: nowrap; /* Prevents text from breaking within tags */
            }
        }
    }
    
    .card-information {
        height: 60%;
        border: 2px solid #06f;
        
        .repo-description {
            margin: 2px;
            overflow: hidden;
            font-size: 25px;
        }
    }
    
    
    .card-button {
        align-items: center;
        background-color: #06f;
        border: 2px solid #06f;
        box-sizing: border-box;
        color: #fff;
        cursor: pointer;
        display: inline-flex;
        fill: #000;
        font-family: Inter,sans-serif;
        font-size: 16px;
        font-weight: 600;
        height: 10%;
        justify-content: center;
        letter-spacing: -.8px;
        line-height: 24px;
        min-width: 140px;
        outline: 0;
        padding: 0 17px;
        text-align: center;
        text-decoration: none;
        transition: all .3s;
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
    }
    
    .card-button:hover {
        background-color: $mainColor;
        border-color: $mainColor;
        fill: #06f;
    }
    
    .card-button:active {
        background-color: $mainColor;
        border-color: $mainColor;
        fill: $mainColor;
    }
}

.portfolio::-webkit-scrollbar{
    display:none;
}
} 
}